import { useEffect, useState, useRef } from 'react';
import Result from '../components/Result';
import Modal from '../components/Modal';

function Home() {
  //     true,  
  const [isOpen, setOpen] = useState(false);
  const handleClickModal = () => setOpen(true);
  const handleModalSubmit = () => setOpen(false);
  const handleModalCancel = () => setOpen(false);

  //  
  const handleClick = () => {
    window.location.href = "/card"
  }

  //  ,  css
  const [ScrollY, setScrollY] = useState(0);
  const [BtnStatus, setBtnStatus] = useState(false);
  // 100    
  const handleFollow = () => {
    setScrollY(window.pageYOffset);
    if(ScrollY > 100) {
      setBtnStatus(true);
    } else {
      setBtnStatus(false);
    }
  }
  //  0 
  const handleTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
    setScrollY(0);
    setBtnStatus(false);
  }

  useEffect(() => {
    const watch = () => {
      window.addEventListener('scroll', handleFollow)
    }
    watch();
    return () => {
      window.removeEventListener('scroll', handleFollow)
    }
  })

  //  
  const useFadeIn = (duration = 1, delay = 0) => {
    const element = useRef();
    useEffect(() => {
      if (typeof duration !== "number" || typeof delay !== "number") {
        return;
      }
      if (element.current) {
        const { current } = element;
        current.style.transition = `opacity ${duration}s ease-in-out ${delay}s`;
        current.style.opacity = 1;
      }
    }, []);
    return { ref: element, style: { opacity: 0 } };
  };
  const fadeInH1 = useFadeIn(3, 1);
  

  return (
    <div>
    <div className='Modal'>
      <button onClick={handleClickModal}></button>
      <Modal
        isOpen={isOpen}
        onSubmit={handleModalSubmit} 
        onCancel={handleModalCancel}
      />
    </div>

    <div className='wrap'>
      <h1 {...fadeInH1}>Re:tter</h1>
      <button onClick={handleClick}></button>
      <div className="inner">
      ## TACOTRON2

Attention  Seq-to-Seq  TTS   

End-to-End  : (, )       

   (MOS)   



TTS   

- Task1 :  Mel-spectrogram  
  - Sequence to Sequence   2  
- Task2 : Mel-spectrogram   
  - Vocoder  WaveNet   



### **1 2 (Seq2Seq)**

input character, output mel-Spectrogram



Encoder : character   hidden(feature) 

Attention : Encoder   hidden     Decoder 

Decoder : Attention    mel-spectrogram 



#### **1.1 **

   input output(label)    

     ,  (, )  (19), (21), (27)    character   

 one-hot encodding  character sequence     input 

3    ( mel-spectrogram  output  )

1. Short-time Fourier Transform(STFT)
   -   (frequency)        Fourier transform 
   -   Fourier transform         sliding window     Fourier Transform 
   -   spectrogram   spectrogram   short-time Fourier Transform 
2. 80 mel filterbank  Mel scaling
   - spectrogram mel-filter bank    (low frequency) 
   -          feature 
   -      feature     scale  
3. Log trnasform
   - log  amplitude  log scaling  mel-spectrogram   output(label) 



#### **1.2 Encoder**

Encoder character  one-hot verctor encoded feature  

Character Embedding, 3 Convolution Layer, Bidirectional LSTM 

input one-hot vector    Embedding matrix  512 embedding vector 

embedding vecotor 3 conv-layer(1d convolution layer + batch norm)  bi-LSTM layer  encoded feature 



####  **1.3 Attention**

Attention   Deocder   Encoder   

Attention Mechanism Encoder LSTM  feature Decoder LSTM    feature  Encoder    alignment 

2  Additive attention mechanism([Bandau Attention](https://joungheekim.github.io/2020/10/08/paper-review/(https://hcnoh.github.io/2018-12-11-bahdanau-attention))) attention alignment    [Location Sensitive Attention](https://paperswithcode.com/method/location-sensitive-attention#) 



#### **1.4 Decoder**

Decoder Attention   alignment feature    mel-spectrogram     mel-spectrogram  

Pre-Net, Decoder LSTM, Projection Layer, Post-Net 

- Pre-Net 2 Fully Connected Layer(256 dim) + ReLU 
  -    mel-spectrogram decoder input    Pre-Net 
  - Pre-Net bottle-neck     

- Decoder LSTM 2 uni-directional LSTM Layer(1024 dim)  
  - Pre-Net   vector  (t1)  context vector(ct1)   Decoder LSTM 

  - Decoder LSTM Attention Layer  Pre-Net     (t)   

  - Decoder LSTM   (t) vector   

    1.     
       1.      Decoder LSTM    vector Fully Connected layer   sigmoid   0 1   (Stop  )
       2.   threshold   inference  mel-spectrogram   

    2. mel-spectrogram  
       1. mel-spectrogram   Decoder LSTM    vector Attention  context vector   Fully Connected Layer 
       2.   mel-vector inference  Decoder   input 

- Post-Net 5 1D Convolution Layer 
  - Convolution Layer 512 filter 51 kernel size  
  -    mel-vector Post-Net    mel-vector (Residual Connection)
  - Post-Net mel-vector    2 Task1   mel-spectrogram   



#### **1.5 2 Loss**

2  mel-spectrogram  mel-spectrogram MSE(mean squared error)   



### **2 WaveNet Vocoder**

Vocoder mel-spectrogram waveform()   

2  WaveNet     Vocoder 

[WaveNet ](https://arxiv.org/abs/1609.03499)   Softmax     215 ~ 215+1      waveform 

  PixelCNN++  [mixture of logistic distribution(MoL)](https://medium.com/@smallfishbigsea/an-explanation-of-discretized-logistic-mixture-likelihood-bdfe531751f0)    215 ~ 215+1     

mel-spectrogram  WaveNet MOL  paramter 

 paramter  215 ~ 215+1     mixture of logistic distribution         waveform 



#### **2.1 WaveNet Loss**

WaveNet  waveform  waveform   Negative log-likelihood Loss   



#### ****

2, WaveNet(MoL)   teacher-forcing 

2    mel-spectrogram encoder featrure    mel-spectrogram 

training  input   2  mel-spectrogram   ground-truth mel-spectrogram    

WaveNet   input WaveNet   waveform    ground-truth waveform 





__

2  https://arxiv.org/abs/1712.05884v2

 https://joungheekim.github.io/2020/10/08/paper-review/

TTS https://sooftware.io/korean_tacotron2/

 https://pytorch.org/hub/nvidia_deeplearningexamples_tacotron2/

        <button className={BtnStatus ? "topBtn active" : "topBtn"} onClick={handleTop}>TOP</button>
      </div>
    </div>
    </div>
  );
}

export default Home;